.homesec_wrap {
    .article1 {
        height: 100vh !important;
        position: relative;
        .main_bg { height: 100%;
            background: url(main.jpg) no-repeat center left;
            background-size: cover; 
        &:before { content: '';
            position: absolute;
            top: 0; left: 0;
            width: 100%; height: 100%;
            background: rgba(0,0,0,0.4); } 
        }
        .text { position: absolute;
            top: 50%; left: 5%;
            transform: translateY(-50%);
            color: #fff;
            h2 { font-size: 65px; }
            p { font-size: 20px; 
                padding: 20px 0; } 
            button { font-size: 18px;
                color: #fff;
                background: transparent;
                border: 2px solid #fff;
                border-radius: 30px; 
                padding: 10px 30px; 
                transition: all 0.5s;
            &:hover { background: #fff;
                color: #0b70b9; } 
            } 
        }
        .login_box { position: absolute;
            top: 50%; right: 10%;
            transform: translateY(-50%);
            padding: 30px;
            background: #fff;
            color: #666;
            form { 
                input { display: block; 
                    height: 40px; 
                    width: 250px;
                    border: none;
                    border-bottom: 1px solid #ddd;
                    margin: 10px 0; } 
                .chk_box { font-size: 15px;
                        vertical-align: middle;
                    input { display: inline-block;
                        height: 15px;
                        width: 15px;
                        margin: 5px 5px 5px 0;
                        vertical-align: top;
                    } 
                    p { display: inline-block; 
                        font-size: 15px; 
                        padding-left: 15px; }
                } 
                button { display: block;
                    width: 100%;
                    background: #0b70b9; 
                    color: #fff;
                    font-size: 15px;
                    padding: 15px 0;
                    margin: 20px 0; 
                } 
                .nonmb_box { font-size: 18px;
                    display: flex;
                    justify-content: space-between;
                    p { 
                        span { padding: 0 10px; 
                            font-size: 15px; 
                            vertical-align: top; }   
                    }
                }
            }
        }
    }
    
    .article2 { 
        .quick { background: #eee; 
            height: 100px; width: 100%;
            .row { display: flex; 
                justify-content: center;
                p { margin: 30px 15px; 
                    padding-right: 15px;
                    font-size: 25px; 
                    transition: all 0.5s;
                    border-right: 1px solid #888;
                    &:hover { color: #0b70b9; }
                    span { padding: 0 20px;
                        line-height: 30px;
                        vertical-align: top; 
                    } 
                }
                p:last-child { border: none; } 
            } 
        }
    }

    .article3 { padding: 100px 0;
        .row {
            .rental_guide { text-align: center;
                h3 { font-size: 35px; }
                ul { padding: 30px 0;
                    li { padding: 15px; 
                        margin: 10px 200px; 
                        color: #fff;
                        font-size: 18px;
                    }
                    li:nth-child(1) { background: #073151; }
                    li:nth-child(2) { background: #0b70b9; }
                    li:nth-child(3) { background: #175758; }
                    li:nth-child(4) { background: #0f5b35; }
                }
            }
        }
    }

    .article4 { position: relative;
        .bg { 
            &:before { position: absolute;
            top: 0; left: 0;
            content: '';
            height: 100%; width: 100%;
            background: rgba(0,0,0,0.4);}
            .bg_text { color: #fff; 
                text-align: center;
                position: absolute;
                top : 50%; left: 50%;
                transform: translate(-50%, -50%);
                p { font-size: 20px;
                    letter-spacing: 2px; } 
                h2 { font-size: 80px; } 
            }
        }
    }
    
    .article5 {
        .grid_box { display: grid;
            grid-template-columns: 50% 50%;
            .grid_item { position: relative;
                img { width: 100%; }
                .grid_text { position: absolute;
                    top: 50%; left: 50%;
                    transform: translate(-50%, -50%);
                    text-align: center;
                    h4 { font-size: 25px; 
                        padding: 10px; }
                }
            }
            // .grid1 {
            //     &:before { position: absolute;
            //         top: 0; left: 0;
            //         width: 100%; height: 100%;
            //         content: '';
            //         background: rgba(100, 149, 237, 0.5); 
            //     }
            // }
        }
    }

    
}