@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&family=Roboto&display=swap);
/* font-family: 'Noto Sans KR', sans-serif;
font-family: 'Roboto', sans-serif; */
* { margin: 0; padding: 0; box-sizing: border-box; outline: none; }

body { font-size: 16px; 
  font-family: 'Noto Sans KR', sans-serif; }


a { color: inherit; text-decoration: none; }
ul, ol { list-style: none; }
.row { max-width: 1600px; margin: 0 auto; padding: 0 80px; }
.cf:after { content: ''; display: block; clear: both;}
img { width: 100%; vertical-align: middle; }
button { cursor: pointer; border: none; }
fieldset { display: none; }
legend { display: none; }
h1, h2, h3, h4, h5, h6 { font-size: 100%; }
table { width: 100%; border-collapse: collapse; table-layout: fixed; } 
.blind { position: absolute; width: 1px; height: 1px; overflow: hidden; text-indent: -99999px; }





.header_wrap{position:fixed;top:0;left:0;width:100%;background:#fff;color:#000;z-index:99;box-shadow:3px 3px 10px rgba(0,0,0,0.5)}.header_wrap .top_nav{height:40px;border-bottom:1px solid #ddd}.header_wrap .top_nav .row{display:flex;justify-content:space-between}.header_wrap .top_nav .row ul{display:flex;padding:10px 20px}.header_wrap .top_nav .row ul li{padding-right:15px;font-size:13px;color:#666}.header_wrap .top_nav .row ul li:last-of-type{padding-right:0}.header_wrap .logo_nav{height:80px}.header_wrap .logo_nav .row{display:flex;justify-content:space-between;align-items:center;vertical-align:middle}.header_wrap .logo_nav .row .logo{padding:20px}.header_wrap .logo_nav .row .logo img{width:100%}.header_wrap .logo_nav .row .nav ul{display:flex}.header_wrap .logo_nav .row .nav ul li{padding:20px 20px;font-size:18px}

