 0.5s;border-right:1px solid #888}.homesec_wrap .article2 .quick .row p:hover{color:#0b70b9}.homesec_wrap .article2 .quick .row p span{padding:0 20px;line-height:30px;vertical-align:top}.homesec_wrap .article2 .quick .row p:last-child{border:none}.homesec_wrap .article3{padding:100px 0}.homesec_wrap .article3 .row .rental_guide{text-align:center}.homesec_wrap .article3 .row .rental_guide h3{font-size:35px}.homesec_wrap .article3 .row .rental_guide ul{padding:30px 0}.homesec_wrap .article3 .row .rental_guide ul li{padding:15px;margin:10px 200px;color:#fff;font-size:18px}.homesec_wrap .article3 .row .rental_guide ul li:nth-child(1){background:#073151}.homesec_wrap .article3 .row .rental_guide ul li:nth-child(2){background:#0b70b9}.homesec_wrap .article3 .row .rental_guide ul li:nth-child(3){background:#175758}.homesec_wrap .article3 .row .rental_guide ul li:nth-child(4){background:#0f5b35}.homesec_wrap .article4{position:relative}.homesec_wrap .article4 .bg:before{position:absolute;top:0;left:0;content:'';height:100%;width:100%;background:rgba(0,0,0,0.4)}.homesec_wrap .article4 .bg .bg_text{color:#fff;text-align:center;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.homesec_wrap .article4 .bg .bg_text p{font-size:20px;letter-spacing:2px}.homesec_wrap .article4 .bg .bg_text h2{font-size:80px}.homesec_wrap .article5 .grid_box{display:grid;grid-template-columns:50% 50%}.homesec_wrap .article5 .grid_box .grid_item{position:relative}.homesec_wrap .article5 .grid_box .grid_item img{width:100%}.homesec_wrap .article5 .grid_box .grid_item .grid_text{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center}.homesec_wrap .article5 .grid_box .grid_item .grid_text h4{font-size:25px;padding:10px}

.footer_wrap{background:#eee}.footer_wrap .row{display:flex;justify-content:space-between}.footer_wrap .row .footer_left{font-size:15px;padding:20px 0}.footer_wrap .row .footer_left .bottom_nav{display:flex;padding-bottom:10px}.footer_wrap .row .footer_left .bottom_nav li{padding-right:20px}.footer_wrap .row .footer_left .bottom_nav li:first-child{color:#0b70b9}.footer_wrap .row .footer_left .address{color:#666;font-size:13px;padding:5px 0}.footer_wrap .row .footer_right{font-size:25px;padding:20px 0}.footer_wrap .row .footer_right p{display:inline-block;padding:0 5px}

